        <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">l-input</span> <span class="hljs-attr">:l-input</span>=<span class="hljs-string">"lInput"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span>/&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">top-title-input</span> <span class="hljs-attr">:g-input</span>=<span class="hljs-string">"gInput"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"电话号码或电子邮件地址"</span>/&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">import</span> { xInput, lInput, TopTitleInput } <span class="hljs-keyword">from</span> <span class="hljs-string">'vo-ui'</span>
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
