<blockquote>
<h2>input 配置</h2>
</blockquote>
<table>
<thead>
<tr>
<th>名称</th>
<th style="text-align:left">说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>:x-input</td>
<td style="text-align:left">普通input 事件</td>
</tr>
<tr>
<td>:l-input</td>
<td style="text-align:left">显形 input 事件</td>
</tr>
<tr>
<td>:g-input</td>
<td style="text-align:left">谷歌input 事件</td>
</tr>
<tr>
