<script>
import { xInput, lInput, TopTitleInput } from 'vo-ui'
export default {
    components: {
        xInput, lInput, TopTitleInput
    },
    data() {
        return {
            checkedType: true,
            checkedAnimationType: true, // 动画组件是否全部 开始播放
            animatedOne: -1, // 动画组件是否无线循环
            tAnimationType: 'enter', // 动画是进入(enter)  还是  离开(leave)
        };
    },
    methods: {
        xInput(e) {
            console.log(e)
        },
        lInput(e) {
            console.log(e)
        },
        gInput(e) {
            console.log(e)
        }
    }
};
</script>


